import {Registry} from 'cs-web-components-base';
import {prefixNS} from './helpers';
import reducer from './reducers/reducers';
import App from './containers/App';
import TaskForms from './containers/TaskForms';

Registry.registerComponent(prefixNS('App'), App);
Registry.registerComponent(prefixNS('TaskForms'), TaskForms);
Registry.registerReducer(prefixNS('reducer'), reducer);

export default {
    App,  // standalone app
    TaskForms  // embedded component
};



