import React from 'react';
import {Icon} from 'cs-web-components-base';
import {prefixNS} from '../helpers';
import {formatStr} from '../i18n';

const ObjectMissing = () => (
    <div className={prefixNS('object_missing')}>
        <Icon name='object_missing'
              title={formatStr('object_missing')} />
        <div className={prefixNS('object_missing__label')}>
            {formatStr('object_missing')}
        </div>
    </div>
);

ObjectMissing.propTypes = {};

export default ObjectMissing;



